.cart{
    width: 40px;
    display: inline-block;
    margin-right: 25px;
}

.cart:hover{
    cursor: pointer;
}

.cart img{
    width: 35px;
    padding-bottom: 4px;
}

.cart span{
    background-color: rgb(255, 145, 0);
    color: white;
    border-radius: 40px;
    font-size: 12px;
    font-weight: 700;
    padding: 1px 5px;
    position: absolute;
}

.avatar{
    display: inline-block;
}

.avatar img{
    width: 40px;
    border: 2px solid rgba(255, 145, 0, 0);
}

.avatar img:hover{
    cursor: pointer;
    border: 2px solid rgb(255, 145, 0);
    border-radius: 25px;
}

.cart-content{
    width: 280px;
    position: absolute;
    background-color: white;
    box-shadow: 2px grey;
    z-index: 2;
}