.desk-menu{
    display: flex;
}

.desk-menu img, .mob-menu img:nth-child(1){
    margin-right: 40px;
}

.desk-menu ul{
    display: inline;
    list-style: none;
    margin: 0;
    padding: 0;
}

.desk-menu li{
    display: inline-block;
    margin: 0 15px;
}

li a{
    text-decoration: none;
    color: black;
    cursor: pointer;
    font-size: 18px;
    font-weight: 500;
}

li a:hover{
    opacity: .8;
}

li a:active{
    opacity: 1;
    text-decoration: underline;
}

.mob-open-menu{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.205);
}

.mob-menu ul{
    background-color: white;
    width: 170px; 
    height:inherit;
    margin: 0;
    list-style: none; 
}

.mob-open-menu img{
    margin-top: 30px;
}

.mob-menu li{
    margin-top: 15px;
}

@media screen and (width < 900px){
    .desk-menu img{
        margin-right: 20px;
    }

    .desk-menu li{
        margin: 0 10px;
    }
}

@media screen and (width < 450px){
    .mob-menu img:nth-child(1){
        margin-right: 15px;
    }
}




