nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid grey;
}

.my-cart{
    width: 320px;
    position: absolute;
    z-index: 3;
    background-color: white;
    border: none;
    border-radius: 15px;
    right: 30px;
    box-shadow: 1px 1px 10px 5px rgba(195, 195, 195, 0.648);
}

.my-cart form{
    width: 90%;
    margin: auto;
}

.my-cart h3{
    margin-left: 5%;
}

.cart-details{
    display: flex;
    flex-wrap: nowrap;
    flex-flow: row;
    justify-content: space-between;
    color: rgb(82, 82, 82);
    font-weight: 500;
}

.delete:hover{
    opacity: 0.85;
    cursor: pointer;
}

.cart-details b{
    color: black;
}

.cart-details img{
    align-self: center;
}

.cart-details img:nth-child(1){
    width: 50px;
    border-radius: 5px;
}

.checkout{
    width: 100%;
    height: 40px;
    margin-bottom: 20px;
    border: none;
    border-radius: 10px;
    background-color: rgb(255, 145, 0);
    color: white;
    font-size: 19px;
    font-weight: bold;
}

.checkout:hover{
    opacity: 0.8;
    cursor: pointer;
}

.checkout:active{
    opacity: 1;
}

section{
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin: 40px 0;
    position: relative;
}

section div{
    width: 40%;
}

.main-img{
    width: 100%;
}

.thumb-holder{
    width: 100%;
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}

.thumb-holder img{
    width: 20%;
    border: 3px solid rgba(255, 145, 0, 0);
}

img.active{
    opacity: 0.4;
    border: 3px solid rgb(255, 145, 0);
}

.thumb-holder img:hover{
    border: 3px solid rgb(255, 145, 0);
}

article img{
    border-radius: 15px;
}

#lightbox{
    position: fixed;
    background-color: rgba(0, 0, 0, 0.302);
    z-index: 2;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

#lightbox > div{
    width: 450px;
    margin: auto;
}

#lightbox > img{
    width: 25px;
    margin: 20px 0;
    display: flex;
    margin-left: auto;
    margin-right: 35px;
}

#main-holder{
    width: 100%;
    position: relative;
}

#iconNext, #iconPrev{
    background-color: rgba(255, 255, 255, 0.722);
    padding: 5px 10px;
    border-radius: 25px;
    width: 20px;
    position: absolute;
    top: 200px;
}

#iconNext:hover, #iconPrev:hover{
    background-color: white;
    color: rgb(255, 145, 0);
}

#iconNext{
    right: -18px;
}

#iconPrev{
    left: -18px
}

.company{
    color: rgb(255, 145, 0);
    text-transform: uppercase;
    font-weight: 700;
}

.highlight{
    font-size: 45px;
}

.description{
    font-size: 18px;
    color: rgb(82, 82, 82);
    font-family: 500;
}

.price{
    display: flex;
    flex-flow: column;
}

.price > div p{
    display: inline;
    margin-right: 10px;
}

.disc-price{
    font-size: 25px;
    font-weight: 700;
}

.discount{
    position: relative;
    bottom: 5px;
    background-color: rgb(255, 145, 0);
    color: white;
    font-size: 14px;
    font-weight: 500;
    border-radius: 5px;
    padding: 1px 3px;
}

.main-price{
    color: rgba(81, 81, 81, 0.645);
    text-decoration: line-through;
    font-size: 18px;
    font-weight: 500;
}

.item-amount{
    display: flex;
    justify-content: space-between;
}

.item-amount > div{
    width: 40%;
    height: 40px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    background-color: rgb(215, 215, 215);
    border-radius: 10px;
}

.item-amount > div button{
    width: 30%;
    margin: 0;
    border: 2px solid rgba(255, 145, 0, 0);
    background: none;
    color: rgb(255, 145, 0);
    font-size: 25px;
    font-weight: 700;
}

.item-amount > div button:hover{
    border: 1px solid rgb(255, 145, 0);
}

.item-amount > div input{
    width: 40%;
    margin: 0;
    border: none;
    padding: auto;
    outline: none;
    font-weight: bold;
    background: none;
    text-align: center;
}

.btn1:hover{
    border: 2px solid rgb(255, 145, 0);
    border-radius: 12px 0 0 12px;
}

.btn2:hover{
    border: 2px solid rgb(255, 145, 0);
    border-radius: 0 12px 12px 0;
}

.btn1:hover, .btn2:hover{
    cursor: pointer;
}

.submit{
    width: 50%;
    border: none;
    border-radius: 12px;
    background-color: rgb(255, 145, 0);
    color: white;
    font-size: 15px;
    font-weight: bold;
}

.submit > img{
    margin-right: 10px;
}

.submit:hover{
    cursor: pointer;
    opacity: 0.8;
}

@media screen and (width < 750px){
    section{
        display: block;
        margin: 0;
        z-index: 0;
    }

    .my-cart{
        box-shadow: 1px 1px 5px 8px rgba(0, 0, 0, 0.186);
    }

    .avatar{
        width: 30px;
    }

    section div{
        width: 100%;
    }

    nav{
        margin: 0 4%;
        border: none;
        position: sticky;
        z-index: 2;
    }

    #lightbox{
        position: relative;
        width: 100%;
        background: none;
    }
    #lightbox > div{
        width: 100%;
    }
    #lightbox .main-img{
        border: none;
        border-radius: 0;
    }
    #iconNext, #iconPrev{
        padding: 1% 3.5%;
        border-radius: 50%;
        width: 10%;
        top: 43%;
    }
    #iconNext{
        right: 10px;
    }
    #iconPrev{
        left: 10px;
    }
    section div:nth-child(2){
        margin: 20px auto;
        width: 90%;
    }
    .mob-menu{
        z-index: 4;
    }
}

@media screen and (width< 450px){
    .my-cart{
        position: relative;
        right: 0;
        box-shadow: none;
        border-top: 1px solid rgb(175, 175, 175);
        border-radius: 0;
        width: 100%;
    }
    .price{
        flex-flow: row;
    }

    p.main-price{
        position: relative;
        bottom: 15px;
    }

    .item-amount{
        flex-flow: column;
    }

    .item-amount div{
        width: 100%;
    }
    
    .submit{
        height: 45px;
        margin: 20px 0;
        width: 100%;
    }
}